@font-face {
  font-family: 'OpensSans';
  src: url("../fonts/opensans-regular.woff2") format("woff2"), url("../fonts/opensans-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

@font-face {
  font-family: 'OpensSans';
  src: url("../fonts/opensans-semibold.woff2") format("woff2"), url("../fonts/opensans-semibold.woff") format("woff");
  font-weight: 600;
  font-style: normal; }

@font-face {
  font-family: 'PlayfairDisplay';
  src: url("../fonts/playfairdisplay-regular.woff2") format("woff2"), url("../fonts/playfairdisplay-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal; }

html,
body {
  box-sizing: border-box;
  max-width: 100%;
  min-width: 320px;
  overflow-x: hidden;
  color: #474747;
  font-family: "OpensSans", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400; }
  @media (min-width: 900px) {
    html,
    body {
      font-size: 16px; } }

body {
  background-color: #e5e5e5;
  margin: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

.wrap {
  margin-left: 20px;
  margin-right: 20px; }
  @media (min-width: 900px) {
    .wrap {
      margin-left: 50px;
      margin-right: 50px; } }
  @media (min-width: 1440px) {
    .wrap {
      width: 1340px;
      margin: 0 auto; } }

.product {
  background-color: #f9f9f9;
  padding-top: 50px;
  padding-bottom: 110px;
  text-align: center; }

.product__title {
  font-weight: 400;
  font-family: "PlayfairDisplay", "Times New Roman", serif;
  line-height: 37px;
  font-size: 28px;
  margin-bottom: 70px;
  color: #003057; }

.product__title-desc {
  font-family: "OpensSans", "Arial", sans-serif; }

.product__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  -webkit-box-pack: justify;
          justify-content: space-between; }
  @media (max-width: 410px) {
    .product__list {
      -webkit-box-pack: justify;
              justify-content: space-between; } }
  @media (max-width: 390px) {
    .product__list {
      -webkit-box-pack: center;
              justify-content: center; } }
  @media (min-width: 1200px) {
    .product__list {
      -webkit-box-pack: start;
              justify-content: flex-start; } }

.product__filter-buttons {
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  -webkit-box-align: center;
          align-items: center;
  -webkit-box-pack: center;
          justify-content: center;
  margin: 0 auto;
  max-width: 1340px;
  width: 85%;
  text-align: center;
  margin-bottom: 50px; }

.product__filter-btn {
  display: block;
  width: 100%;
  padding-top: 16px;
  padding-bottom: 15px;
  padding-left: 15px;
  padding-right: 15px;
  margin-bottom: 5px;
  border: none;
  text-align: center;
  border-radius: 3px;
  font-size: 10px;
  line-height: 10px;
  letter-spacing: 0.15em;
  font-family: "OpensSans", "Arial", sans-serif;
  font-weight: 600;
  color: white;
  -webkit-transition: 0.6s;
  transition: 0.6s;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #007fa3;
  max-width: 200px;
  margin-right: 10px; }
  .product__filter-btn:focus, .product__filter-btn:hover {
    outline: none;
    opacity: 0.9; }
  .product__filter-btn:active {
    opacity: 0.7; }
  @media (min-width: 900px) {
    .product__filter-btn {
      font-size: 12px;
      line-height: 12px;
      padding-top: 20px;
      padding-bottom: 18px; } }
  .product__filter-btn.active {
    background-color: #12b2a6; }

.product__item {
  width: 48%;
  margin: 0;
  margin-bottom: 20px;
  padding: 0;
  padding: 20px;
  border-radius: 23px;
  display: -webkit-box;
  display: flex;
  flex-wrap: wrap;
  -webkit-box-align: center;
          align-items: center;
  background-color: white;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25); }
  @media (max-width: 410px) {
    .product__item {
      margin-right: 0;
      width: 176px; } }
  @media (min-width: 1000px) {
    .product__item {
      text-align: left; } }
  @media (min-width: 1200px) {
    .product__item {
      max-width: 32%;
      margin-right: 2%; }
      .product__item:nth-child(3n) {
        margin-right: 0; } }

.product__num {
  display: -webkit-box;
  display: flex;
  align-self: flex-start;
  -webkit-box-align: center;
          align-items: center;
  -webkit-box-pack: center;
          justify-content: center;
  padding-bottom: 5px;
  width: 82px;
  height: 82px;
  margin: 0 auto;
  margin-bottom: 20px;
  border-radius: 50%;
  font-family: "PlayfairDisplay", "Times New Roman", serif;
  line-height: 35px;
  font-size: 26px;
  color: #007fa3;
  background-color: #f7f7f7; }
  @media (min-width: 1000px) {
    .product__num {
      text-align: left;
      margin-left: 0; } }

.product__title-desc {
  width: 100%;
  align-self: flex-start;
  line-height: 30px;
  font-size: 22px;
  font-weight: 600;
  margin: 0 auto;
  margin-top: 0;
  margin-bottom: 20px;
  color: black; }
  @media (min-width: 1000px) {
    .product__title-desc {
      width: 100%; } }

.product__desc {
  width: 100%;
  margin: 0;
  margin-bottom: 20px; }
  @media (min-width: 1000px) {
    .product__desc {
      width: 70%; } }

.footer {
  padding-top: 73px; }

.footer__logo {
  display: block;
  text-align: center;
  margin-bottom: 60px; }

.footer__info {
  border-top: solid 1px #b9b9b9;
  padding-top: 25px;
  padding-bottom: 10px; }

.footer__info-item {
  display: block;
  text-align: center;
  color: #727272;
  margin-bottom: 25px; }

@media (min-width: 1050px) {
  .footer__info {
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: start;
            justify-content: flex-start;
    -webkit-box-align: center;
            align-items: center; }
  .footer__info-item {
    margin-right: 40px;
    font-size: 14px;
    line-height: 14px; } }

.header {
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
  background-color: white;
  padding-top: 15px;
  padding-bottom: 20px;
  margin-bottom: 50px; }

.header__wrap {
  background-color: white; }
  .header__wrap .wrap {
    padding: 20px;
    padding-top: 15px;
    padding-bottom: 15px;
    display: -webkit-box;
    display: flex;
    -webkit-box-pack: justify;
            justify-content: space-between;
    -webkit-box-align: center;
            align-items: center; }

.header__logo {
  display: block;
  margin-left: -10px;
  width: 130px;
  height: 55px; }
  .header__logo img {
    width: 100%; }

@media (min-width: 900px) {
  .header .wrap {
    padding-left: 52px;
    padding-right: 52px; }
  .header__logo {
    width: 160px;
    margin-top: -15px; } }

.home-page .home-page__title {
  margin: 0 auto;
  max-width: 1340px;
  width: 85%;
  text-align: center;
  margin-bottom: 50px;
  font-family: "OpensSans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 38px;
  line-height: 1.3em; }
